.layout {
  display: -webkit-flex;
  display: flex;
  height: 100%;
  width: 100%;
}

.layout__main {
  width: 100%;
}

.key {
  background: transparent;
  border: 1px solid lightgray;
  display: inline-block;
  margin: 0.5em;
  padding: 0.5em;
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease;
}

.key_active {
  -webkit-transition: none;
  transition: none;
  background: lightgreen;
}

.key-item {
  display: block;
  padding: 6px;
  white-space: nowrap;
}

.geo-json {
  display: block;
  padding: 6px;
  white-space: nowrap;
}

.tile-radio {
  display: block;
  padding: 6px;
  white-space: nowrap;
}

.map {
  height: 100%;
  width: 100%;
}


.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
}

.app__nav {

}

.app__nav-link {
  display: inline-block;
  margin: 5px;
}

.app__content {
  display: -webkit-flex;
  display: flex;
  height: 100%;
}

fieldset {
  border: 1px solid lightgray;
}

html, body, #root {
  height: 100%;
  width: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

